<template>
  <div class="video-detail">
    <common-header :options="headerOptions"></common-header>
    <div class="title_content"><span class="title">作者: {{author}}</span></div>
    <div>
      <video-player v-if="source" ref="player" :option="option" :poster="poster" :source="source" :time="time">
      </video-player>
    </div>
    <operate-buttons ref="operate-buttons" :resourceId="resourceId" @load-item="loadItem"></operate-buttons>
    <operation-btns-popups ref="popups" :resourceId="resourceId" :catalog="catalog" :operations="operationList"></operation-btns-popups>
  </div>
</template>

<script>
  import CommonHeader from '../common/CommonHeader.vue'
  import VideoPlayer from 'vue-h-video-play'
  import OperateButtons from './OperateButtons.vue'
  import OperationBtnsPopups from './OperationBtnsPopups.vue'
  import {Toast} from 'mint-ui'
  export default {
    components: {CommonHeader, OperateButtons, VideoPlayer, Toast, OperationBtnsPopups},
    data () {
      return {
        resourceId: this.$route.params.resourceID,
        author: '',
        headerOptions: {title: '音视频详情', align: 'center',backMethod : function(){
          router.push('/resourceMobile/product');
        }},
        item: {},
        catalog:[],
        operationList: [],
        option: {
          width: '100%',
          height: '200px',
          playMode: 'inline',//inline or fullScreen or defalut(默认行为ios 全屏,android行内)
          playIcon: 'time', //default or time
          autoPlay: false,//true or false
          /*beforePlay: function () {
           //可以再这里加上是否是wifi环境的判断
           return true;
           }*/
        },
        poster: '',
        source: '',
        time: '开始'
      }
    },


    mounted: function () {
      this.resourceId = this.$route.params.resourceID;
      this.loadDetailInfo();
      this.loadResDetailData();

    },

    methods: {
      loadItem : function (item) {
        this.item = item;
        this.catalog = item.extendAttributes.slice(0,5);
      },
      loadResDetailData: function () {
        let that = this;
        var url = this.$global.getUrlByAppId("PORTAL") + "/loadResourceDetailData?userId=" + this.$global.userId;
        let para = {};
        para.resourceId = this.resourceId;
        this.$http.post(url, qs.stringify({para: JSON.stringify(para)}))
          .then(function (response) {
            that.source = response.data.resource.fullFileName;
            that.poster = response.data.resource.iconUrl;
          })
          .catch(function (response) {
            Toast("获取失败");
          })
      },
      loadDetailInfo: function () {
        let that = this;
        var url = this.$global.getUrlByAppId("PORTAL") + "/loadDetailInfo/" + this.resourceId;
        this.$http.get(url).then(function (response) {
          console.log(response);
          that.author = response.data.resou.creatorName;
          that.operationList = response.data.operationlist;
        }).catch(function () {
          Toast("获取失败");
        })
      },
    },
  }
</script>


<style lang="scss">
  .video-detail {
    font-size: 16px;
    height: 100%;
    img {
      width: 90%;
      height: 100%;
    }
    .title_content {
      margin-top: 15%;
      padding-bottom: 20px;
      padding-left: 1.5em;
      padding-right: 1.5em;
      --width: 100vw;
      box-sizing: border-box;
    }
    .title {
      width: 90vw;
      --font-size: 22px;
      word-wrap: break-word;
      word-break: break-all;
      display: inline-block;
      line-height: 35px;
    }

    .footer {
      position: fixed;
      width: 99%;
      height: 10%;
      bottom: 0px;
      box-sizing: border-box;
    }
  }
</style>



