<template>
  <div class="query-tab-bottom-down time-search" :class="[!headerOptions.visiable?'head-hidden':'']">
    <common-header v-if="headerOptions.visiable" :options="headerOptions"></common-header>
    <div class="flex">
      <input @click="openByDialog" :value="calendar4.display" readonly>
      <img v-show="calendar4.display" src="../../assets/image/close.png" alt="" @click="clear">
    </div>
    <transition name="fade">
      <div class="calendar-dialog">
        <calendar :range="calendar4.range" :zero="calendar4.zero" :lunar="calendar4.lunar" :value="calendar4.value"
                  @select="calendar4.select"></calendar>
      </div>
    </transition>
    <div class="bottom-button">
      <mt-button type="default" size="small" @click="clear()">重置</mt-button><mt-button type="danger" size="small" @click="commit()">确定</mt-button>
    </div>

  </div>
</template>

<script>
  import CommonHeader from '../common/CommonHeader.vue'
  import calendar from '../common/calendar.vue'
  import $ from 'jquery'
  export default {
    components: {
      CommonHeader, calendar
    },
    data() {
      return {
        beginTime : '',
        endTime : '',
        headerOptions: {
          visiable : true,
          align: 'center',
          title: '时间筛选',
          styleObject: {'background-color': '#ffffff', 'color': '#333'},
          backMethod: this.opts.backMethod
        },
        calendar4: {
          display: this.initDisplay(this.opts.selected),
          show: false,
          range: true,
          zero: true,
          value: this.initSelected(this.opts.selected), //默认日期
          lunar: true, //显示农历
          select: (begin, end) => {
            this.calendar4.show = false;
            this.calendar4.value = [begin, end];
            this.calendar4.display = begin.join("-") + " ~ " + end.join("-");
            this.beginTime = begin.join("-");
            this.endTime = end.join("-");
          }
        }
      }
    },
    props : ['opts'],
    mounted() {
      if(this.opts.headerOptions)
        $.extend(this.headerOptions, this.opts.headerOptions);
    },
    methods: {
      openByDialog() {
        this.calendar4.show = true;
      },
      closeByDialog() {
        this.calendar4.show = false;
      },
      initSelected : function(val){
        if(!val || val.length === 0) return [];
        var v1 = val[0];
        var v2 = val[1];
        var v1a = v1.split('-');
        var v2a = v2.split('-');
        var value = [];
        value.push(v1a);
        value.push(v2a);
        return value;
      },
      initDisplay : function (val) {
        if(!val || val.length === 0) return '';
        var v1 = val[0];
        var v2 = val[1];
        return v1 + " ~ "  + v2;
      },
      commit : function(){
		this.opts.callBack(this.beginTime,this.endTime);
      },
      clear : function(){
        this.calendar4.value = [];
        this.calendar4.display = '';
        this.beginTime = '';
        this.endTime = '';
        var $cal = $('.time-search');
        for(var i=0;i<$cal.length;i++){
          if($($cal[i]).css('display') != 'none'){
            $($cal[i]).find('.calendar td.selected').removeClass('selected')
          }
        }
      }
    },
    watch : {
      selected : function(val){
        this.initSelected(val);
      },
      opts : function(val){
        if(val.selected && val.selected.length == 0){
          this.clear();
        }
      }
    }
  };
</script>

<style lang="scss">
  .query-tab-bottom-down.time-search {
    margin-top: 38px;
    background-color: #ffffff;
    width: 100%;
    border-bottom: 1px solid #dedede;
    border-top: 1px solid #dedede;
    .flex {
      border-radius: 2px;
      --margin: 10px;
      position: relative;
    }
    .flex > input {
      box-sizing: border-box;
      width: 100%;
      border-radius: 2px;
      border: 1px solid #dedede;
      padding: 10px;
      font-size: 14px;
      background: url(data:image/svg+xml;base64,) no-repeat 8px 10px;
      padding-left: 36px;
      color: #666;
      position: relative;
    }
    .flex > img{
      position: absolute;
      top: 10px;
      right: 10px;
    }
    /*transition*/
    .fade-enter-active,
    .fade-leave-active {
      transition: all .5s ease-in-out;
    }
    .fade-enter, .fade-leave-active {
      opacity: 0;
      transform: translateY(-10px);
    }
    /*下拉框*/
    .calendar-dropdown {
      background: #fff;
      position: absolute;
      left: 0;
      top: 0;
      padding: 10px 20px 10px 20px;
      border: 1px solid #eee;
      border-radius: 2px;
    }
    .calendar-dropdown:before {
      position: absolute;
      left: 30px;
      top: -10px;
      content: "";
      border: 5px solid rgba(0, 0, 0, 0);
      border-bottom-color: #DEDEDE;
    }
    .calendar-dropdown:after {
      position: absolute;
      left: 30px;
      top: -9px;
      content: "";
      border: 5px solid rgba(0, 0, 0, 0);
      border-bottom-color: #fff;
    }
    /*弹出框*/
    .calendar-dialog {
      background: #fff;
      padding: 10px 20px 10px 20px;
      border: 1px solid #eee;
      border-radius: 2px;
    }
    .bottom-button button{
      width:50%;
      padding:0px;
    }
  }
</style>



