<script>
  export default {

    props: {
      save: {
        default: function () {
          console.log('save')
        }
      },
      commit: {
        default: function () {
          console.log('commit')
        }
      }
    }
  }
</script>

<style lang="scss">

  .footBtns {
    .save {
      float: left;
      margin-left: 15%;
    }

    .commit {
      float: right;
      margin-right: 15%;
    }

    .saveBtn {
      height:20px;
      width: 20px;
      background-image: url(../../assets/image/icon/mini保存.svg);
    }
    .commitBtn {
      height:20px;
      width: 20px;
      background-image: url(../../assets/image/icon/mini提交.svg);
    }
  }


</style>



