<template>
  <div class="my-popup">
    <!--审核弹窗-->
    <mt-popup v-model="auditVisible" popup-transition="popup-fade" class="mint-popup-1">
      <div>审核意见</div>
      <div>
        <el-input placeholder="请输入审核意见" class="advice" style="" type="textarea" v-model="auditAdvice"
                  :rows="6"></el-input>
      </div>
      <mt-button class="pass" style="" size="small" type="danger" @click="auditPass">审核通过</mt-button>
      <mt-button class="back" style="position: absolute; bottom: 10%; right: 20%;" size="small" type="danger"
                 @click="auditBack">审核打回
      </mt-button>
    </mt-popup>
    <!--编目弹窗-->
    <mt-popup v-model="catalogVisible" popup-transition="popup-fade" position="bottom" class="mint-popup-1">
      <!--<div class="catalog">
        <div class="cata-content" v-for=" attr in catalog" v-if="attr.value">
          <span>{{attr.name}} : </span><span>{{attr.value}}</span></div>
      </div>-->
      <el-table
        :data="catalog"
        style="width: 100%; margin-top: 5%"
        :show-header= false>
        <el-table-column
          prop="id">
        </el-table-column>
        <el-table-column
          prop="value">
        </el-table-column>
      </el-table>
    </mt-popup>
    <!--记录弹窗-->
    <mt-popup v-model="recordVisible" popup-transition="popup-fade" position="bottom" class="mint-popup-1">
      <el-table
        :data="operations "
        border
        style="width: 100%"
        height="250">
        <el-table-column
          prop="operator.uname"
          label="操作人"
          width="100">
        </el-table-column>
        <el-table-column
          prop="operateDescribe"
          label="操作"
          width="80">
        </el-table-column>
        <el-table-column
          prop="created"
          label="操作时间"
          width="170">
        </el-table-column>
        <el-table-column
          prop="detailOperation"
          label="操作描述"
          width="150">
        </el-table-column>
      </el-table>
    </mt-popup>

    <!--附件弹窗-->
    <mt-popup v-if="ccid == '富文本类'" v-model="attachmentsVisible" popup-transition="popup-fade" position="bottom" class="mint-popup-1">
      <el-table
        :data="attachments"
        style="width: 100%; margin-top: 5%"
        :show-header= false>
        <el-table-column
          prop="fileName">
        </el-table-column>
      </el-table>
    </mt-popup>
  </div>
</template>
<script>
  import {Toast} from 'mint-ui'
  import ElInput from "../../../node_modules/element-ui/packages/input/src/input";
  export default {
    data() {
      return {
        auditVisible: false,
        catalogVisible: false,
        recordVisible: false,
        attachmentsVisible:false,
        auditAdvice: ''
      }
    },
    components: {
      ElInput,
      Toast
    },
    props: ['resourceId', 'catalog', 'operations', 'attachments', 'ccid'],

    methods: {
      auditPass: function () {

        if (this.auditAdvice.length == 0) {
          Toast({
            message: '请填写审核意见',
            position: 'bottom'
          })
          return;
        }
        let that = this;
        let url = this.$global.getUrlByAppId("PORTAL") + '/resource/' + that.resourceId + '/audit?userId=' + this.$global.userId;

        this.$http({
          url: url,
          data: JSON.stringify({"status": "审核通过", "comment": that.auditAdvice}),
          method: 'post',
          headers: {'Content-Type': 'text/plain'}
        })
          .then(function (response) {

            that.auditVisible = false;
            Toast('审核通过');
            router.push({name: 'product', params: {resourceId: that.resourceId, operator: 'delete'}})

          })
          .catch(function (error) {
            console.log(error.response.data);
          })
      },
      auditBack: function () {
        if (this.auditAdvice.length == 0) {
          Toast({
            message: '请填写审核意见',
            position: 'bottom'
          })
          return;
        }
        let that = this;
        let url = this.$global.getUrlByAppId("PORTAL") + '/resource/' + that.resourceId + '/audit?userId=' + this.$global.userId;

        this.$http({
          url: url,
          data: JSON.stringify({"status": "被驳回", "comment": that.auditAdvice}),
          method: 'post',
          headers: {'Content-Type': 'text/plain'}
        })
          .then(function (response) {
            that.auditVisible = false;
            Toast('审核驳回');
            router.push({name: 'product', params: {resourceId: that.resourceId, operator: 'delete'}})
          })
          .catch(function (error) {
            console.log(error.response.data);
          })
      },
    },

  }
</script>

<style lang="scss">
  .my-popup {
    .mint-popup-1 {
      width: 96% !important;
      height: 250px;
    }
    .catalog {
      margin-top: 15%;
    }
    .cata-content {
      height: fit-content;
      text-indent: 0.5em;
      font-size: 20px;
      margin-bottom: 2%;
      margin-top: 5%;
    }
    .mint-popup-1 {
      margin-bottom: 15%;
      font-size: 30px;
      text-indent: 0.6em;
    }
    .advice {
      width: 86%
    }

    .pass {
      position: absolute;
      bottom: 10%;
      left: 20%;
    }
    .back {
      position: absolute;
      bottom: 10%;
      right: 20%;
    }

    div.cell {
      width: fit-content;
    }

    th{
      div.cell {
        padding: 0px;
        height: 24px;
        width: 78px;
        margin-left: -15%;
      }
    }
    td{
      div.cell {
        text-indent:0em;
      }
    }
  }

</style>



