<template>
  <div id="header" class="header">
    <mt-header fixed :title="titleCenter" v-bind:style="styleObject">
      <mt-button slot="left" icon="back" @click="headerBack(options.backMethod)">{{ titleLeft }}</mt-button>
      <mt-button v-if="moreOpera.visiable" icon="more" slot="right" @click="showMoreOpera()"></mt-button>
      <mt-button v-if="options.queryVisiable" icon="search"  slot="right" @click="headerBack(options.showQuery)">{{ titleRight }}</mt-button>
    </mt-header>
    <mt-popup class="query-popup query-tab-bottom-popup"
              v-model="popupVisible" :modal="modal" :closeOnClickModal="closeOnClickModal"
              position="bottom">
      <div class="more-opera" v-for="item in moreOpera.operas" @click="callMethod(item.method)">{{item.title}}
      </div>
    </mt-popup>
  </div>
</template>
<script>
  import $ from 'jquery'

  export default {
    name: 'CommonHeader',
    props: {
      options: {
        type: Object,
        default: function () {
          return {
            align: 'center',
            title: '',
            queryVisiable:false,
            styleObject: {}
          }
        }
      },
      moreOpera: {
        type: Object,
        default: function () {
          return {
            visiable: false,
            operas: []
          }
        }
      }
    },
    mounted() {
      if (this.options.styleObject)
        $.extend(this.styleObject, this.options.styleObject);
    },
    data() {
      return {
        popupVisible: false,
        closeOnClickModal: true,
        modal: true,
        styleObject: {'background-color': '#ef4f4f', 'font-size': '16px'}
      }
    },
    methods: {
      headerBack: function (method) {
        if (method) {
          method();
        } else {
          router.go(-1);
        }
      },
      showMoreOpera: function () {
        if (!this.moreOpera.operas) return;
        this.popupVisible = true;
      },
      callMethod: function (method) {
        method();
        this.popupVisible = false;
      }
    },
    computed: {
      titleLeft: function () {
        return this.options.align === 'left' ? this.options.title : '';
      },
      titleCenter: function () {
        return this.options.align === 'center' ? this.options.title : '';
      },
      titleRight: function () {
        return this.options.align === 'right' ? this.options.title : '';
      }
    }
  }
</script>
<style lang="scss">
.header{
  .is-fixed{
    z-index: 9999;
  }
  font-size : 16px;
  .more-opera {
    line-height: 40px;
    height: 40px;
    padding: 5px 5px 5px 25px;
    border-top: 1px solid #dedede;
  }
  .query-popup {
    width: 100%;
    height: auto;
  }
}
</style>



